import React, { useState } from "react";
import ReactDOM from "react-dom/client";
import axios from "axios";
import { jsPDF } from "jspdf";
import "bootstrap/dist/css/bootstrap.min.css";
import "./index.css";
import "./data/tellsis-Regular-normal.js";
import transInfoData from "./data/translate.json";
import transPrepareData from "./data/proper-noun.json";

function App() {
  const [inputText, setInputText] = useState("あいしてる");
  const targetLanguageKey = "en";
  const [resultEnglishText, setResultEnglishText] = useState("I love you");
  const [resultTmailText, setResultTmailText] = useState("I love you");

  const properNounData = () => {
    return transPrepareData.join("\n");
  };

  const translateText = () => {
    const encodedParams = new URLSearchParams();
    encodedParams.append("to", targetLanguageKey);
    encodedParams.append("text", inputText);

    const options = {
      method: "POST",
      url: "https://translo.p.rapidapi.com/api/v3/translate",
      headers: {
        "content-type": "application/x-www-form-urlencoded",
        "X-RapidAPI-Key": "bdbe9e48e7mshc40360d6e4aa05ep160e6cjsncaca092c755c",
        "X-RapidAPI-Host": "translo.p.rapidapi.com",
      },
      data: encodedParams,
    };

    axios
      .request(options)
      .then(function (response) {
        var he = require("he");
        const outputText = he.decode(response.data.translated_text);

        const properNounSet = new Set(transPrepareData);
        const tellsisText = outputText.replace(/\w+/g, (word) =>
          properNounSet.has(word)
            ? word
            : word.replace(/[A-Za-z]/g, (c) => transInfoData[c])
        );

        console.log("Roman Text:" + outputText);
        console.log("TellsisText:" + tellsisText);
        setResultEnglishText(outputText);
        setResultTmailText(tellsisText);
        document.getElementById("translated").removeAttribute("style");
      })
      .catch(function (error) {
        console.error(error);
      });
  };

  const transToTellsis = () => {
    const properNounSet = new Set(transPrepareData);
    const tellsisText = inputText.replace(/\w+/g, (word) =>
      properNounSet.has(word)
        ? word
        : word.replace(/[A-Za-z]/g, (c) => transInfoData[c])
    );

    console.log("Roman Text:" + inputText);
    console.log("TellsisText:" + tellsisText);
    setResultEnglishText("");
    setResultTmailText(tellsisText);
    document.getElementById("translated").removeAttribute("style");
  };

  const pdfDownload = () => {
    const doc = new jsPDF();

    doc.setFont("Language Of Tellsis-Regular");
    doc.text(resultTmailText, 15, 50);

    const currentDate = new Date();
    const dateString =
      currentDate.getFullYear() +
      "" +
      (currentDate.getMonth() + 1 < 10 ? "0" : "") +
      (currentDate.getMonth() + 1) +
      "" +
      (currentDate.getDate() < 10 ? "0" : "") +
      currentDate.getDate() +
      "" +
      (currentDate.getHours() < 10 ? "0" : "") +
      currentDate.getHours() +
      "" +
      (currentDate.getMinutes() < 10 ? "0" : "") +
      currentDate.getMinutes() +
      "" +
      (currentDate.getSeconds() < 10 ? "0" : "") +
      currentDate.getSeconds();
    doc.save(dateString + "_tellsis.pdf");
  };

  const showDetail = () => {
    if (document.getElementById("textDetail").style.display === "none") {
      document.getElementById("textDetail").removeAttribute("style");
    } else {
      document.getElementById("textDetail").style.display = "none";
    }
  };

  return (
    <div className="App">
      <section className="tool">
        <section>
          <h2>入力してください。</h2>
          <textarea
            id="input"
            className="translate input"
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
          ></textarea>
          <button
            className="col-sm-2"
            color="orange"
            size="large"
            onClick={translateText}
          >
            Translate
          </button>
          <button
            className="col-sm-3"
            color="orange"
            size="large"
            onClick={transToTellsis}
          >
            Turn to Tellsis
          </button>
        </section>
        <section id="translated" style={{ display: "none" }}>
          <button color="orange" size="large" onClick={pdfDownload}>
            PDF Download
          </button>
          <h2>Tellsis</h2>
          <textarea
            id="tellsis"
            className="tellsis translate"
            value={resultTmailText}
            readOnly
          ></textarea>
          <button color="orange" size="large" onClick={showDetail}>
            Details
          </button>
          <form id="textDetail" style={{ display: "none" }}>
            <div className="form-group row">
              <label className="col-sm-2">English</label>
              <div className="col-sm-10">
                <textarea
                  className="form-control"
                  id="staticEnglish"
                  value={resultEnglishText}
                  readOnly
                ></textarea>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-sm-2">Tmail</label>
              <div className="col-sm-10">
                <textarea
                  className="form-control"
                  id="tmail"
                  value={resultTmailText}
                  readOnly
                ></textarea>
              </div>
            </div>
          </form>
        </section>

        <section>
          <h2>ProperNoun</h2>
          <textarea
            id="properNoun"
            className="proper-noun"
            defaultValue={properNounData()}
          ></textarea>
        </section>
      </section>
    </div>
  );
}

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<App />);
